body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    text-align: center;
}

a {
    cursor: pointer;
}

.app .breadcrumb:last-child {
    color: rgba(255,255,255,0.7);
}

.app .breadcrumb.active {
    color: #fff;
}

.level-one-objects-grid img {
    object-fit: cover;
    width: 100%;
    max-height: 250px;
    max-width: 250px;
    cursor: pointer;
}

.level-one-objects-grid .row {
    display: flex;
    justify-content: center;
}

.level-one-objects-grid .row .col.s4 {
    display: flex;
    width: auto;
    margin-left: 0;
}

.level-one-objects-grid .image-grid {
    margin-top: 2em;
}

.level-one-objects-grid .selected {
    border: 3px solid #cccbcb;
    opacity: 0.7;
}

@media screen and (max-width: 600px) {
    .level-one-objects-grid .row .col.s4 {
        max-height: 100px;
    }
}
