.level-one-objects-grid img {
    object-fit: cover;
    width: 100%;
    max-height: 250px;
    max-width: 250px;
    cursor: pointer;
}

.level-one-objects-grid .row {
    display: flex;
    justify-content: center;
}

.level-one-objects-grid .row .col.s4 {
    display: flex;
    width: auto;
    margin-left: 0;
}

.level-one-objects-grid .image-grid {
    margin-top: 2em;
}

.level-one-objects-grid .selected {
    border: 3px solid #cccbcb;
    opacity: 0.7;
}

@media screen and (max-width: 600px) {
    .level-one-objects-grid .row .col.s4 {
        max-height: 100px;
    }
}